@import "variables";
@import "mixins";
html,
body {
	background: #fff;
    font-family: 'Helvetica-Regular';
    font-size: 14px;
}
body {
    &.no_scroll {
        overflow: hidden;
    }
}
.wrapper {
    max-width: 1150px;
    padding-left: 10px;
    padding-right: 10px;
    margin: 0 auto;
}
a {
    text-decoration: none;
}
h3 {
    font-weight: normal;
    font-family: $light;
    font-size: 20px;
    color: #2E2E2E;
    margin-bottom: 25px;
}
.mob {
    &_btn {
        margin-right: 30px;
        white-space: nowrap;
        @media (max-width:920px) {
            cursor: pointer;
        }
        i {
            display: none;
            vertical-align: middle;
            @media (max-width:970px) {
                display: inline-block;
            }
        }
        b {
            font-weight: normal;
            @media (max-width:970px) {
                display: none;
            }
        }
        &_search {
            display: none!important;
            @media (max-width:700px) {
                display: block!important;
            }
        }
        &_filter {
            padding: 12px;
            background: $red;
            color: #fff;
            font-size: 16px;
            position: fixed;
            left: 0;
            cursor: pointer;
            top: 45%;
            z-index: 22;
            display: none!important;
            text-align: center;
            @media (max-width:850px) {
                display: block!important;
            }
        }
    }
    &_close {
        width: 30px;
        height: 30px;
        position: absolute;
        top: 0;
        right: 0;
        z-index: 5;
        display: none;
        @media (max-width:660px) {
            display: block;
        }
        span {
            display: block;
            position: absolute;
            left: 50%;
            top: 50%;
            width: 25px;
            height: 2px;
            background: $red;
            margin: -1.5px 0 0 -12.5px;
            border-radius: 5px;
            transform: rotate(45deg);
            & + span {
                transform: rotate(-45deg);
            }
        }
    }
}
.dd {
    &_block {
        position: relative;
        
        a {
            white-space: nowrap;
        }
        &:hover {
            & > div {
//                &:first-child {
//                    background: #fff;
//                    color: $red;
//                }
                &:last-child {
                    display: block;
                }
            }
        }
        &_top {
            padding: 5px 15px;
            border: 1px solid #fff;
            border-radius: 4px;
            text-transform: uppercase;
        }
        &_bottom {
            position: absolute;
            top: 100%;
            left: 0%;
            right: 0;
            text-align: center;
            border: 1px solid #fff;
            border-radius: 4px;
            background: $red;
            display: none;
            a {
                display: block;
                    padding: 4px 15px;
                &:hover {
                    background: #fff;
                    color: red;
                }
            }
        }
    }
}
input.sub {
    width: 100%;
    display: block;
    padding: 11px;
    font-size: 16px;
    border-radius: 2px;
    border: 1px solid #E4E4E4;
    margin-top: 20px;
    margin-bottom: 20px;
}
input {
    &[type=checkbox],
    &[type=radio] {
        display: none;
        & + span {
            width: 22px;
            height: 22px;
            display: inline-block;
            background: #fff;
            border: 1px solid #D4D4D4;
            border-radius: 4px;
            vertical-align: middle;
            margin-right: 7px;
        }
        &:checked {
            & + span {
                background: $red url(../images/checked.png) no-repeat center;
                border-color: $red
            }
        }
    }
}
