.section {
//    overflow: hidden;
}
.home_slider {
    .owl-stage-outer {
        overflow: visible!important;
    }
    img {
        width: 100%;
    }
    .owl-item:not(.active) {
        filter: grayscale(100%);
    }
    .owl-nav button.owl-prev, 
    .owl-nav button.owl-next {
        position: absolute;
        left: -35px;
        font-size: 45px;
    }
}
.btn_more {
    margin-top: 25px;
}
.btn_more:hover a {
    cursor: pointer;
    text-decoration: none;
}
.lr {
    &_block {
        display: flex;
        justify-content: space-between;
        align-items: stretch;
    }
    &_right,
    &_left {
        width: 49%;
    }
    &_col {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    &_index {
        @media (max-width:990px) {
            display: block;
            .lr_right,
            .lr_left {
                width: 100%;
                margin: 15px 0;
            }
        }
    }
}
.footer_copy_left img + img {
    min-width: 58px;
    vertical-align: bottom;
}
.header_cab {
    position: relative;
}
#hidden_content {
    position: absolute;
    top: 100%;
    right: 5px;
}
