@import "variables";
@import "mixins";
@import "flex";
@import "reset";
@import "base";
@import "layout";

@import "modules/all";
@import "components/all";

@import "media";

@font-face {
  font-family: 'Helvetica-Regular';
  src: url('../fonts/Helvetica-Regular.eot');
  src: url('../fonts/Helvetica-Regular.woff2') format('woff2'),
       url('../fonts/Helvetica-Regular.woff') format('woff'),
       url('../fonts/Helvetica-Regular.ttf') format('truetype'),
       url('../fonts/Helvetica-Regular.svg#Helvetica-Regular') format('svg'),
       url('../fonts/Helvetica-Regular.eot?#iefix') format('embedded-opentype');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Helvetica-Light';
  src: url('../fonts/Helvetica-Light.eot');
  src: url('../fonts/Helvetica-Light.woff2') format('woff2'),
       url('../fonts/Helvetica-Light.woff') format('woff'),
       url('../fonts/Helvetica-Light.ttf') format('truetype'),
       url('../fonts/Helvetica-Light.svg#Helvetica-Light') format('svg'),
       url('../fonts/Helvetica-Light.eot?#iefix') format('embedded-opentype');
  font-weight: normal;
  font-style: normal;
}
