[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
//  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^="icon-"] img, [class*=" icon-"] img {
    display: inline-block;
}
.bx-blue .btn.btn-default {
    background: $red;
    border-color: $red;
    &:hover {
        background: #fff;
        color: $red;
        border-color: $red;
    }
}
.fty {
    text-align: center;
    .page-not-found {
        font-size: 20px;
        font-family: $light;
        margin-bottom: 35px;
    }
    &_num {
        font-size: 160px;
        color: $red;
        margin-bottom: 35px;
        font-weight: bold;
    }
    &_btn {
        text-align: center;
        a {
            display: inline-block;
            max-width: 270px;
            width: 100%;
            margin: 0 15px;
        }
    }
}
.search {
    &-item {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        padding: 10px 0;
        & + & {
            border-top: 1px solid rgba(0,0,0,0.1);
        }
        &-image {
            min-width: 150px;
            margin-right: 15px;
            img {
                display: block;
                margin: 0 auto;
            }
        }
    }
}
.product-item-info-container {
    margin: 0 !important;
}