.btn {
    background: #F4F4F4;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #000;
    font-size: 20px;
    border-radius: 4px;
    padding: 13px 7px;
    .sale {
        color: #858585;
        position: relative;
        display: inline-block;
        padding-left: 4px;
        padding-right: 1px;
        margin-right: 5px;
        &:after {
            content: '';
            display: block;
            position: absolute;
            top: 50%;
            left: 0;
            right: 0;
            height: 2px;
            background: #858585;
        }
    }
    &.red {
        color: #fff;
        background: $red;
        border: 1px solid $red;
        text-align: center;
        &:hover {
            border-color: $red;
            background: #fff;
            color: $red;
        }
    }
    &:hover {
        background: #D0021B;
        color: #fff;
        .sale {
            color: #fff;
            opacity: 0.9;
            &:after {
                background:  #fff;  
            }
        }
    }
    &_more {
        text-align: center;
        margin-bottom: 55px;
        a {
            display: inline-block;
            padding: 15px 25px;
            border: 1px solid #B4B4B4;
            border-radius: 4px;
            color: #4C4C4C;
            font-family: $light;
            font-size: 16px;
            
        }
        img {
            margin-right: 15px;
            & + img {
                display: none;
            }
        }
        &:hover {
            
            a {
                color: #fff;
                background: $red;
                border-color: $red;
            }
            img {
                display: none;
                & + img {
                    display: inline-block;
                }
            }
        }
    }
}


.section {
    
    &_head {
        color: #171717;
        font-size: 28px;
        position: relative;
        text-align: center;
        margin-bottom: 7px;
//        font-family: $light;
        .all {
            font-size: 16px;
            color: $red;
            position: absolute;
            top: 50%;
            margin-top: -8px;
            right: 0;
            @media (max-width:450px) {
                position: relative;
                display: block;
                margin-top: 15px;
            }
            &:hover {
                color: #171717;
            }
        }
    }
    &_block {
        margin-bottom: 3px;
    }
}



.slider {
    &_goods {
        @media (max-width:1250px) {
            margin: 0 45px;
            width: auto;
        }
        .owl-nav {
            button {
                width: 34px;
                height: 111px;
                border: none;
                border-radius: 6px;
                background-color: #F6F6F6!important; 
                text-indent: -99999px;
                position: absolute;
                top: 50%;
                margin-top: -80.5px;
                background-repeat: no-repeat!important; 
                background-position: center!important; 
                &.owl-prev {
                    background-image: url(../images/prev.png) ;
                    left: -50px;
                    &:hover {
                        background: $red url(../images/prev_hover.png) no-repeat center !important;
                    }
                }
                &.owl-next {
                    background: url(../images/next.png) no-repeat center;
                    right: -50px;
                    &:hover {
                        background: $red url(../images/next_hover.png) no-repeat center !important;
                    }
                }
                
            }
        }
    }
}





