.aside {
    &_block {
        margin-bottom: 30px;
    }
    &_close {
        width: 35px;
        height: 35px;
        border-radius: 50%;
        background: $red;
        display: inline-block;
        position: relative;
        span {
            width: 25px;
            height: 2px;
            background: #fff;
            transform: rotate(45deg);
            position: absolute;
            left: 50%;
            top: 50%;
            margin: -1px 0 0 -12.5px;
            & + span {
                transform: rotate(-45deg);
            }
        }
    }
}
