            display: flex;
            @media (max-width:800px) {
                width: 100%!important;
                margin: 20px 0;
                justify-content: center;
                
                & > * {
                    display: inline-block;
                    margin: 0 15px;
                }
            }
            
        }
    }
 
            &_item {
                position: relative;
                &_img {
                    width: 15%;
                    @media (max-width:680px) {
                        width:30%!important;
                        min-width:30%!important;
                    }
                }
                &_info {
                    width: 46%;
                    text-align: left;
                    padding-left: 10px!important;
                    @media (max-width:820px) {
                        width: 30%;
                    }
                    @media (max-width:680px) {
                        width:70%!important;
                        min-width:70%!important;
                        text-align: left!important;;
                    }
                }
                &_cost {
                    width: 10%;
                    text-align: center;
                    @media (max-width:820px) {
                        width: 18%;
                    }
                }
                .add {
                    width: 14%;
                }
                
                &_sum {
                    width: 10%;
                    text-align: center;
                    @media (max-width:820px) {
                        width: 18%;
                    }
                }
                &_last {
                    width: 70px;
                    text-align: center;
                    @media (max-width:680px) {
                        position: absolute;
                        top: 6px;
                        right: 0px;
                        text-align: right !important;
                    }
                }
                //list
                @media (max-width:680px) {
                    flex-wrap: wrap;
                }
                & > * {
                    @media (max-width:680px) {
                        display: block;
                        width: 30%;
                        min-width: 30%;
                        text-align: center;
                        justify-content: center;
                        &:before {
                            content: attr(data-info);
                            opacity: 0.7;
                            display: inline-block;
                            margin-right: 10px;
                        }
                    }
                    @media (max-width:400px) {
                        &:before {
                            display: block;
                            text-align: center;
                        }
                    }
                    
                }
            }

    &_head {
        text-align: center;
        font-family: $light;
        background: #f2f2f2;
        font-size: 16px;
        @media (max-width:680px) {
            display: none;
        }
        & ~ * {
            padding: 7px 0;
            & + * {
                border-top: 1px solid #f4f4f4;
            }
        }
        & > * {
            padding: 12px 0;
        }
        
    }
    &_bottom {
        flex-wrap: wrap;
        padding-top: 15px;
    }
}

.news {
    display: block!important;
    margin-right: 20px;
        width: 100%;
    @media (max-width:850px) {
        margin-right: 0;
    }
    &_item {
        font-family: $light;
        margin-bottom: 50px;
        &_name {
            color: #2E2E2E;
            font-size: 22px;
            margin-bottom: 10px;
            display: block;
            font-weight: normal;
            &:hover {
                color: $red;
            }
        }
        &_date {
            font-size: 16px;
            color: #2E2E2E;
            margin-bottom: 20px;
        }
        &_tag {
            color: $red;
            display: inline-block;
            margin-left: 15px;
        }
        &_img {
            display: block;
            margin-bottom: 20px;
            img {
                display: block;
                width: 100%;
                height: auto;
            }
        }
        &_info {
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            p {
                font-size: 16px;
            }
            @media (max-width:550px) {
                display: block;
                .btn {
                    margin-left: 0!important;
                    margin-top: 25px;
                    display: inline-block;
                }
            }
        }
        .btn {
            white-space: nowrap;
            background: #fff;
            color: $red;
            border: 1px solid $red;
            font-size: 16px;
            
            padding-left: 25px;
            padding-right: 25px;
            
            &:hover {
                background: $red;
                color: #fff;
            }
        }
        p {
            & + .btn {
                margin-left: 25px;
            }
        }
    }
    &_index {
        display: flex;
        justify-content: space-between;
        align-items: stretch;
        @media (max-width:850px) {
            flex-wrap: wrap;
        }
        .news {
            &_item {
                padding: 15px;
                border: 1px solid #dedede;
                border-radius: 5px;
                margin-bottom: 20px;
                @media (min-width:850px) {
                    & + .news_item {
                        margin-left: 15px;
                    }
                }
            }
        }
    }
    & + aside {
        margin-right: 0px;
        padding-top: 77px;
        display: flex;
        justify-content: flex-start;
        flex-direction: column;
        align-items: flex-start;
    }
    &-pagenavigator {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        
        b,
        a {
            white-space: nowrap;
            background: #fff;
            color: #E31D23;
            border: 1px solid #E31D23;
            font-size: 16px;
            padding: 15px 23px;
            margin: 7px 7px; 
            /* padding-right: 25px; */
            display: inline-block;
            border-radius: 5px;
            font-weight: normal;
            &:hover {
                color: #fff;
                background: #E31D23;
            }
        }
        b {
            color: #fff;
            background: #E31D23;
        }
        a {
            @media (max-width:680px) {
                order: 1;
                &:last-child,
                &:first-child {
                    order: 2;
                    width: calc( 50% - 15px );
                    text-align: center;
                }
            }
        }
    }
}

.page {
    font-family: $light;
    p {
        margin-bottom: 25px;
        font-size: 16px;
        color: #2E2E2E;
        line-height: 22px;
    }
    .news_item_name {
        &:hover {
            color: inherit;
        }
    }
}

blockquote {
    padding: 20px;
    border-radius: 5px;
    background: #EAEAEA;
    font-size: 16px;
    color: #2E2E2E;
    line-height: 22px;
    margin-bottom: 25px;
}

aside {
    .right {
        display: none;
    }
    @media (max-width:850px) {
        position: fixed;
        top: 0;
        left: -280px;
        bottom: 0;
        min-width: 320px;
        background: #fff;
        z-index: 25;
        overflow: hidden;
        overflow-y: auto;
        margin: 0;
        transition: left ease 0.4s;
        &.open {
            left: 0;
        }
        .right {
            display: block;
            text-align: right;
        }
    }
}
.beforehand {
    padding: 20px;
    border: 1px solid #DCDCDC;
    border-radius: 5px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    h2 {
        font-size: 24px;
        color: #181818;
        font-weight: 900;
        margin-bottom: 20px;
        
        font-weight: normal;
    }
    .goods_item_tag {
        position: relative;
    }
    &_name {
        margin-top: 20px;
        font-size: 20px;
        color: #181818;
        font-weight: 500;
        line-height: 26px;
        max-width: 275px;
        margin-bottom: 16px;
        font-family: $light;
        
        display: block;
        &:hover {
            color: $red;
        }
    }
    &_info {
        font-family: $light;
        color: #373737;
        font-size: 16px;
        line-height: 22px;
    }
    &_img {
        text-align: center;
        display: block;
    }
    &_btn {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-top: 15px;
        @media (max-width:530px) {
            display: block;
        }
        .cost {
            min-width: 30%;
            text-align: center;
        }
        .btn {
            padding-left: 25px;
            padding-right: 25px;
            font-family: $light;
            @media (max-width:530px) {
                margin-top: 20px;
            }
        }
    }
    .lr {
        &_block {
            @media (max-width:530px) {
                display: block;
            }
        }
    }
}

.sale {
    &_block {
        padding: 30px;
        background: #2B2A29;
        border-radius: 6px;
        color: #fff;
        font-family: $light; 
        font-size: 16px;
        h2 {
            font-size: 24px;
            
            font-weight: normal;
            margin-bottom: 20px;
        }
    }
    &_top {
        display: flex;
        justify-content: flex-start;
        align-items: stretch;
        margin-bottom: 20px;
        
        img {
            margin-right: 15px;
            @media (max-width:480px) {
                display: none;
            }
        }
    }
}
.sub {
    padding: 30px;
    border: 1px solid #DCDCDC;
    border-radius: 5px;
    margin-top: 20px;
    h2 {
        font-size: 24px;
            
        font-weight: normal;
        margin-bottom: 20px;
    }
    input {
        border: 1px solid #DCDCDC;
        border-radius: 5px;
        padding: 10px 15px;
        font-family: $light;
    }
    button {
        background: #2B2A29;
        text-align: center;
        color: #fff;
        font-size: 20px;
        border-radius: 4px;
        padding: 12px 20px;
        font-family: $light;
        margin-left: 15px;
        @media (max-width:1136px) and (min-width:990px) {
            margin-left: 0;
            margin-top: 15px;
        }
        @media (max-width:570px) {
            margin-left: 0;
            margin-top: 15px;
        }
        &:hover {
            background: $red;
        }
    }
    &_form {
        padding-top: 20px;
        form {
            display: flex;
            align-items: stretch; 
            justify-content: space-between;
            @media (max-width:1136px) and (min-width:990px) {
                display: block;
            }
            @media (max-width:570px) {
                display: block;
            }
        }
    }
    &_footer { 
        * {
            background: #F4F4F4;
            display: flex;
            justify-content: center;
            align-items: center;
            color: #000;
            font-size: 20px;
            border-radius: 4px;
            padding: 13px 7px;
        }
    }
}

.order {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    @media (max-width:970px) {
        flex-wrap: wrap;
    }
    &_block {
        width: 30%;
        @media (max-width:970px) {
            width: 48%;
        }
        margin-bottom: 35px;
        @media (max-width:650px) {
            width: 100%;
        }
    }
    &_cont {
        padding: 20px;
        border: 1px solid #dddddd;
        border-radius: 5px;
        height: calc( 100% - 50px);
        display: flex;
        flex-direction: column;
        align-items: stretch;
        justify-content: space-between;
    }
    input {
        &:not([type="radio"]) {
            padding: 10px;
            border: 1px solid #dddddd;
            width: 100%;
        }
        &[type="radio"] {
            & + span {
                border-radius: 50%;
                cursor: pointer;
                min-width: 22px;
            }
        }
    }
    label {
        width: 100%;
    }
    &_row {
        display: flex;
        margin-bottom: 10px;
        label + label {
            margin-left: 10px;
        }
    }
    b {
        display: block;
        margin-bottom: 12px;
    }
    &_check {
        display: flex;
        align-items: center;
        div {
            display: flex;
            justify-content: space-between;
            width: 100%;
        }
    }
    &_head {
        font-size: 16px;
        margin-bottom: 20px;
        display: flex;
        align-items: center;
        white-space: nowrap;
        span ~ span {
            color: #646464;
            font-size: 14px;
            padding-left: 5px;
        }
        &_num {
            font-size: 16px;
            color: #fff;
            background: $red;
            width: 28px;
            min-width: 28px;
            height: 28px;
            display: inline-block;
            margin-right: 10px;
            text-align: center;
            border-radius: 50%;
            vertical-align: middle;
            padding-top: 3px;
        }
    }
    & + .center {
        margin-bottom: 55px;
        .btn {
            max-width: 300px;
            text-align: center;
            width: 100%;
            margin: 0 auto;
        }
    }
}
