.header {
    @media (min-width:970px) {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        z-index: 113;
        background: #fff;  
        & + * {
            margin-top: 145px;
        }
        & + .section .slider {
            padding-top: 20px;
        }
        
    }
    
    
    &_top {
        .wrapper {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
        font-size: 14px;
        a {
            &:not(.mob_close) {
                color: $grey;
                text-decoration: none;
                     padding: 9px 0 9px 0;
                display: inline-block;
                &:hover {
                    color: $red;
                }
                & + a:not(.mob_close) {
                    margin-left: 30px;
                }
            }
            
        }
//        .wrapper {
//            max-width: 1000px;
//        }
        .mob_btn {
            display: none;
            color: $grey;
            padding: 10px;
            @media (max-width:660px)  {
                display: block;
            }
        }
        &_nav {
            @media (max-width:660px)  {
//                display: none;
                position: fixed;
                top: 0;
                left: -321px;
                bottom: 0;
                overflow: hidden;
                overflow-x: auto;
                width: 320px;
                background: #fff;
                border-right: 1px solid $red;
                z-index: 23;
                transition: left ease 0.4s;
                &.open {
                    left: 0px;
                }
                a:not(.mob_close) {
                    display: block;
                    padding-left: 30px;
                    margin-left: 0!important;
                    border-left: 2px solid #fff;
                    margin-bottom: 1px;
                    &:hover,
                    &.current {
                        border-left: 2px solid $red;
                        color: $red;
                    }
                }
            }
        }
    }
    &_bottom {
        background: #E31D23;
            padding-top: 16px;
        padding-bottom: 11px;
        color: #fff;
        &_right {
            width: 100%;
            margin-left: 80px;
            @media (max-width:700px) {
                margin-left: 0px;
            }
        }
        &_nav,
        &_info {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
        &_info {
                padding-bottom: 21px;
            @media (max-width:970px) {
                padding-bottom: 0px;
                flex-wrap: wrap;
            }
            
                & > * {
                    @media (max-width:700px) {
//                        &:not(.header_search) {
//                            margin-bottom: 20px;
//                        }
                    }
                    @media (max-width:500px) {
                        &:not(.header_cab) {
                            order: 2;

                        }
                    }
                }
           
            .mob_btn {
                display: none;
                @media (max-width:970px) {
                    display: block;
                    margin-right: 10px;
                }
            }
        }
        &_nav {
            justify-content: flex-start;
            font-size: 16px;
            @media (max-width:970px) {
                .mob_btn {
                    display: none;
                    
                }
            }
        }
        a {
            color: #fff;
        }
        .wrapper {
            display: flex;
            align-items: center;
            justify-content: space-between;
            @media (max-width:700px) {
                display: block;
            }
        }
    }
    
    &_search {
        width: 100%;
        position: relative;
        max-width: 465px;
        @media (max-width:970px) {
            max-width: 100%;   
            margin-bottom: 20px;
        }
        @media (max-width:700px) {
            display: none;
            order: 5!important;
            margin-bottom: 0;
            margin-top: 20px;
        }
        
        input {
                padding: 15px 40px 15px 20px;
            display: block;
            width: 100%;
            border-radius: 5px;
            background: #fff;
            border: none;
            color: #000;
        }
        button {
            position: absolute;
            top: 0;
            bottom: 0;
            right: 0;
            width: 40px;
            background: url(../images/search.png) no-repeat center left;
            color: $grey;
        }
    }
    &_cab {
        min-width: 200px;
        white-space: nowrap;
        margin: 0 10px;
        a {
            & + a {
                margin-left: 15px;
            }
        }
        i {
            font-style: normal;
        }
        @media (max-width:500px) {
            order: 1;
            width: 100%;
            text-align: center;
            margin-bottom: 15px;
        }
    }
    .dd_block {
        min-width: 68px;
    }
}
nav {
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    @media (max-width:970px) {
        display: none;
        a {
            display: block;
            padding: 5px 10px;
        }
    }
    a {
        &:hover {
            text-decoration: underline;
        }
        & + a {
            margin-left: 10px;
            @media (max-width:970px) {
                margin-left: 0;   
            }
        }
    }
}
.logo {
    width: 171px;
    transition: all ease 0.4s;
    img {
        width: 100%;
        transition: all ease 0.4s;
    }
    &.min {
        width: 120px;
        img {
            
        }
        
    }
    @media (max-width:700px) {
        display: block;
        
        text-align: center;
        padding-bottom: 20px;
    }
}
header {
    margin-bottom: 20px;
    & + .section {
        overflow: hidden;
    }
}