
.footer {
    &_top {
        border-top: 1px solid #E7E7E7;
        border-bottom: 1px solid #E7E7E7;
        font-family: $light;
        .wrapper {
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            @media (max-width:900px) {
                flex-wrap: wrap;
            }
        }
        a {
            display: block;
        }
        &_block {
            width: 30%;
            position: relative;
            padding: 68px 30px 88px 30px;
            &:before {
                content: '';
                display: block;
                position: absolute; 
                top: 0;
                bottom: 0;
                width: 1px;
                left: 0;
                
                background: #ffffff;
                background: -moz-linear-gradient(top, #ffffff 0%, #e1e1e1 50%, #ffffff 100%);
                background: -webkit-linear-gradient(top, #ffffff 0%,#e1e1e1 50%,#ffffff 100%);
                background: linear-gradient(to bottom, #ffffff 0%,#e1e1e1 50%,#ffffff 100%);
                filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#ffffff',GradientType=0 );
            }
            &:first-child {
                @media (max-width:900px) {
                    width: 100%;
                    padding: 20px 30px 10px 30px;
                    .footer_top_head_main {
                        margin-bottom: 0;
                    }
                    & ~ * {
                        padding-top: 15px;
                    }
                }
                @media (max-width:500px) {
                    padding: 10px 0px;
                }
                @media (max-width:730px) {
                    & ~ * {
                        width: 100%;
                        padding: 0 30px;
                        .footer_top_head {
                            margin-bottom: 10px;
                            cursor: pointer;
                            i {
                                display: inline-block;
                                vertical-align: top;
                                margin-top: -2px;
                                margin-left: 10px;
                            }
                            & + div {
                                display: none;
                                a {
                                    display: block;
                                }
                            }
                        }
                        &:before {
                            display: none;
                        }
                    }
                }
                @media (max-width:500px) {
                     & ~ * {
                        padding: 0 0px;
                    }
                }
                &:before {
                    display: none;
                }
                & + * {
                    &:before {
                        display: none;
                    }
                }
            }
            a {
                display: b51lock;
                color: #3A3A3A;
                font-size: 16px;
                margin-bottom: 15px;
                &:hover {
                    color: $red;
                }
            }
            span {
                display: inline-block;
                vertical-align: middle;
                margin-right: 10px;
                border-radius: 50%;
                background: #EBEBEB;
                width: 33px;
                height: 33px;
            }
        }
        &_head {
            font-size: 18px;
            color: #171717;
            margin-bottom: 30px;
            i {
                display: none;
            }
            &_main {
                font-size: 28px;
                    margin-top: -12px;
                i {
                    display: block;
                    font-style: normal;
                }
            }
        }
    }
    &_bottom {
        background: #FDFDFD;
        font-size: 16px;
        padding-top: 45px;
        font-family: $light;
        &_left {
            & > div {
                &:first-child {
                    display: flex;
                    margin-bottom: 45px;
                    @media (max-width:830px) {
                        justify-content: center;   
                    }
                    @media (max-width:550px) {
                        display: block;
                        text-align: center;
                    }
                    .logo {
                            margin-right: 30px;
                    }
                    & + div {
                        color: #474747;
                        max-width: 267px;
                        @media (max-width:830px) {
                            margin: 0 auto;
                        }
                    }
                }
                a:not(.logo) {
                    
                    margin-top: 6px;
                    
                }
            }
        }
        &_right {
            width: 55%;
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            @media (max-width:830px) {
                width: 100%;   
            }
            @media (max-width:550px) {
                display: block;
                text-align: center;
                .footer_bottom_block {
                    margin-bottom: 25px;
                }
                .footer_bottom_head {
                        margin-bottom: 24px;
                }
            }
            a {
                margin-bottom: 12px;
            }
        }
        .wrapper {
            display: flex;
            justify-content: space-between;
            @media (max-width:830px) {
                flex-direction: column;
                & > div {
                    width: 100%;
                }
            }
        }
        &_head {
            font-size: 20px;
            margin-bottom: 30px;
            i {
                display: none;
            }
            @media (max-width:730px) {
                            i {
                                display: inline-block;
                                vertical-align: top;
                                margin-top: -2px;
                                margin-left: 10px;
                            }
                            & + div {
                                display: none;
                                a {
                                    display: block;
                                }
                            }
                 
            }
        }
        a {
            display: block;
            color: #404040;
            &:hover {
                color: $red;
            }
        }
    }
    &_copy {
        margin-top: 20px;
        .wrapper {
            display: flex;
            justify-content: space-between;
            align-items: center;
            flex-wrap: wrap;
            @media (max-width:1024px) {
                flex-direction: column;
            }
        }
        &_left { 
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 100%;
            max-width: 760px;
            margin-right: 20px;
            margin-bottom: 20px;
            @media (max-width:700px) {
                flex-direction: column;
            }
            & > * {
                display: flex;
                justify-content: space-between;
                align-items: center;
                max-width: 270px;
                @media (max-width:700px) {
                    margin-bottom: 20px;  
                    justify-content: center;
                    br {
                        display: none;
                    }
                }
                & > *:first-child {
                    margin-right: 15px;
                }
            }
            img {
                display: inline-block;
            }
        }
        &_right {
            margin-bottom: 20px;
        }
    }
}
